%visuallyhidden {
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  position: absolute; }

%bg-cover {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; }

@mixin imgBg($padding) {
  @include backgroundSize(cover);
  background-position: center;
  background-color: #eee;
  display: block;
  &:before {
    content: "";
    display: block;
    padding: $padding; } }

@mixin absfull() {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

@mixin bgcover() {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; }
