            height: 1px;
            background: $primary;
            margin: 0 5px;
            vertical-align: middle;
            transition: all .3s ease; }
          &:after {
            content: "";
            background: $primary;
            position: relative;
            vertical-align: middle;
            width: 15px;
            height: 15px;
            border: 3px solid #FFF;
            display: inline-block;
            position: absolute;
            top: 6px;
            left: 0;
            right: 0;
            margin: 0 auto;
            transition: all .3s ease; } } } } } }


// Contact
.contact {
  .contact-wrapper {
    padding: 80px 0; }
  .item-contact {
    width: 100%;
    border: 1px solid $secondary;
    padding: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 24px;
    transition: all .3s ease;
    flex-direction: column;
    .icon {
      display: inline-block;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background: $secondary;
      margin-bottom: 20px; }
    h4 {
      font-weight: bold; }
    b {
      display: inline-block;
      margin-right: 5px; }
    a {
      color: #000; }
    p {
      margin-bottom: 0; }
    &:hover {
      border-color: #000; } }
  .contact-form {
    padding-top: 40px;
    h3 {
      font-weight: bold;
      font-size: 36px;
      line-height: 1.5em;
      text-align: center; } } }

// Cart

.cart {
  &-empty {
    padding: 90px 0;
    width: 100%;
    display: flex;
    align-content: center;
    text-align: center;
    justify-content: center;
    flex-direction: column;
    .box-img {
      margin-bottom: 30px; }
    label {
      font-size: 20px;
      font-weight: bold;
      margin-bottom: 20px; } }
  &-content {
    padding: 90px 0;
    .img-box {
      width: 180px;
      position: relative;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      margin-right: 20px;
      &:before {
        content: "";
        padding: 50% 0;
        display: block; } }
    .text-box {
      font-size: 15px;
      h4 {
        font-weight: 700;
        margin-bottom: 15px; }
      span {
        display: block;
        margin-bottom: 10px;
        &.price,
        &.total-price {
          font-weight: 700; } }
      .total-item {
        margin-bottom: 15px; }
      b {
        display: inline-block;
        margin-right: 5px; }
      .total-item {
        height: 40px;
        width: 140px;
        background: #ccc; } } }
  &-item {
    display: flex;
    padding-bottom: 20px;
    border-bottom: 1px solid #cccccc; }
  &-order-summary {
    padding: 0 0 20px 0;
    margin-bottom: 30px;
    margin-bottom {}
    .subtotal {
      display: flex;
      justify-content: space-between;
      font-weight: 700;
      margin-bottom: 15px; } }

  &-get-shipping {
    .form-group {
      padding: 0;
      padding-bottom: 15px;
      border: 0; } }
  &-item-action {
    padding-top: 20px;
    display: flex;
    justify-content: space-between; }

  &-title {
    padding: 15px;
    background: #f5f5f5;
    border-bottom: 1px solid rgba(0,0,0,0.15);
    font-size: 15px;
    line-height: 1.1em;
    font-weight: 700; } }


// Checkout

.checkout {
  margin-top: -100px;
  position: relative;
  &:after {
    content: "";
    width: 46%;
    height: 100vh;
    border-left: 1px solid $border;
    background: #fafafa;
    position: absolute;
    top: 0;
    right: 0; }
  &-r,
  &-l {
    padding-top: 4em;
    position: relative;
    z-index: 3; }
  &-l {
    padding-right: 6%; }
  &-r {
    padding-left: 6%; }
  .header-logo {
    img {} }
  label {
    font-weight: 500; }
  .breadcrumb {
    ul {
      display: flex;
      flex-wrap: wrap;
      margin-top: 1em;
      li {
        display: flex;
        align-items: center;
        a,
        span {
          font-size: 0.85714em;
          color: $secondary; }
        span {
          color: #333; }
        .svg {
          width: 10px;
          height: 10px;
          transform: rotate(-90deg);
          margin: 0 0.64286em; } } } }
  .fg-heading {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 10px;
    label {
      width: auto;
      font-weight: 500; }
    p {
      font-size: 14px;
      a {
        color: $secondary; } } }
  .form-control {
    border: 1px #d9d9d9 solid;
    background-clip: padding-box;
    border-radius: 5px;
    display: block;
    box-sizing: border-box;
    width: 100%;
    padding: 0.92857em 0.78571em;
    word-break: normal;
    line-height: inherit;
    height: auto;
    max-height: 45px;
    margin-bottom: 15px; }
  .form-group {
    &.filed {
      .form-control {
        padding-top: 1.5em;
        padding-bottom: 0.35714em; } } }
  .floating-label {
    font-size: 0.85714em;
    font-weight: normal;
    position: absolute;
    top: 0;
    width: 100%;
    margin-top: 0.42857em;
    margin-left: 1px;
    padding: 0 0.91667em;
    z-index: 1;
    user-select: none;
    transform: translateY(3px);
    pointer-events: none;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    box-sizing: border-box;
    opacity: 0; }
  .form-action {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center; }
  .btn-primary {
    width: auto;
    padding: 1.4em 1.7em;
    border-radius: 6px; }
  .btn-link {
    padding: 0;
    font-size: 14px;
    color: $secondary;
    text-transform: capitalize;
    &:hover {
      color: $secondary; } }

  &-product-summary {}
  &-ps-item {
    padding-bottom: 15px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    border-bottom: 1px solid $border;
    margin-bottom: 20px;
    .img-box {
      width: 65px;
      height: 65px;
      border: 1px solid $border;
      border-radius: 6px;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      margin-right: 10px; }
    .text-box {
      width: calc(100% - 120px); }
    h4 {
      font-size: 15px;
      line-height: 1em;
      margin-bottom: 0;
      font-weight: 700; }
    .detdesc {
      font-size: 12px;
      color: #717171; }
    .price {
      text-align: right;
      font-weight: 700;
      font-size: 14px; } }
  &-voucher {
    padding-bottom: 20px;
    border-bottom: 1px solid $border;
    margin-bottom: 20px; }
  &-subtotal {
    li {
      display: flex;
      justify-content: space-between;
      span {
        font-size: 14px;
        &:last-child {
          font-weight: 700; } }
      &.total-final {
        border-top: 1px solid $border;
        margin-top: 20px;
        padding-top: 20px;
        span {
          &:last-child {
            font-size: 18px;
            small {
              font-size: 14px;
              font-weight: 400; } } } } } } }
